@import url(https://fonts.googleapis.com/css2?family=Fira+Sans+Condensed:wght@300&family=Montserrat&display=swap);
body {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Montserrat', sans-serif;
}


.wrap{
    display: flex;
    justify-content: center;
    align-items: center;
}
.skeleton{
    width: 80%;
    height: 94vh;
    margin: 30px 30px 0 30px;
    position: relative;
}
.lines{
    display: flex;
    justify-content: center;
    align-items: center;
    overflow: hidden;
}
.lines .line-1{
    position: absolute;
    background-color: #6d6d6d;
    width: 50%;
    height: 15px;
    border-radius: 4px;
    margin-top: 55px;
}
 .lines .line-2{
    position: absolute;
    background-color: #6d6d6d;
    width: 25%;
    height: 7px;
    border-radius: 4px;
    margin-top: 100px;
}
.lines .line-3{
    position: absolute;
    background-color: #6d6d6d;
    width: 65%;
    height: 400px;
    border-radius: 10px;
    margin-top: 550px;
}
.lines .line-4{
    position: absolute;
    background-color: #6d6d6d;
    width: 65%;
    height: 35px;
    border-radius: 10px;
    margin-top: 994px;
}
.lines .line-5{
    position: absolute;
    background-color: #6d6d6d;
    width: 50%;
    height: 15px;
    border-radius: 4px;
    margin-top: 1095px;
}
 .lines .line-6{
    position: absolute;
    background-color: #6d6d6d;
    width: 25%;
    height: 7px;
    border-radius: 4px;
    margin-top: 1145px;
}
.lines .line-7{
    position: absolute;
    background-color: #6d6d6d;
    width: 65%;
    height: 35px;
    border-radius: 8px;
    margin-top: 1211px;
}
.line-1::before,
.line-2::before,
.line-3::before,
.line-4::before,
.line-5::before,
.line-6::before,
.line-7::before{
        content: '';
        position: absolute;
        height: 100%;
        width: 100%;
        background-image: linear-gradient(to right, #d9d9dd 0%, rgba(0,0,0,0.05) 20%, #d9d9d9 100%);
        background-size: 100% 100%;
        background-repeat: no-repeat;
        animation: shimmer 1.5s linear infinite;
}
.line-3::before,
.line-4::before,
.line-5::before
{
    animation-delay: 0.4s;
}
@keyframes shimmer{
    0%{
        background-position: -450px 0;
        opacity: 0.3;
    }
    100%{
        background-position: 450px 0;
        opacity: 0;
    }
}
section{
    width: 100%;
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
section h3{
    text-align: center;
    padding: 0;
    margin: 25px 0 10px 0;
}
section .underline{
    width: 200px;
    border: 3px solid rgb(250, 71, 6);
    border-radius: 15px;
    margin-top: 5px;
    margin-bottom: 20px;
}
.wrapperPlace{
     margin-top: 40px;
}
.footer{
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    border-radius: 5px;
    box-shadow: inset 200px 200px 181px -200px rgba(0,0,0,0.76);
}
.imgpricename{
    width: 570px;
    height: 490px;
    margin-bottom: 80px;
    box-shadow: 10px 10px 24px 0px rgba(0,0,0,0.75);
}
.imgpricename img{
    width:100%;
    height: 100%;
}
footer p{
    font-weight: 600;
    color: rgb(255, 44, 7);
}
footer .namefooter{
   font-weight: 700;
   color: rgb(77, 31, 77);
}
footer button{
    background-color: rgb(248, 8, 8);
    padding: 4px;
    font-weight: 600;
    border: none;
    outline: none;
    cursor: pointer;
    color: white;
    border-radius: 4px;
}
footer button span{
    text-align: center;
    justify-content: center;
    align-items: center;
    background-color: oldlace;
    padding: 2px;
    color: rgb(34, 33, 33);
    font-weight: 700;
    border-radius: 5px;
}
footer .visit{
    color: White;
    font-weight: 700;
}
footer button:hover{
    opacity: 0.8;
}
.task{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.task .notask{
    font-weight: 700;
    margin-top: 20px;
    font-size: 20px;
    color: crimson;
}
.task button{
    background-color: rgb(248, 8, 8);
    padding: 4px;
    font-weight: 600;
    border: none;
    outline: none;
    cursor: pointer;
    color: white;
    border-radius: 4px;
}
.task button:hover{
    opacity: 0.8;
}
.manteer{
    margin-bottom: -11px;
    font-size: 25px;
    display: flex;
}

.target{
    font-size: 23px;
    transform: translateX(62px);
    animation: target 0.4s linear infinite;
}
@keyframes target{
    0%{
        transform: skewY(0);
        transform: translateX(62px);
    }
    50%{
        transform: skewY(-10deg);
        transform: translateX(62px) translateY(1px);
    }
    100%{
        transform: skewY(0deg);
        transform: translateX(59px) translateY(2px);
    }
}
.manteer .teer{
   transform: translateY(-11px) translateX(25px);
   animation: teer 0.3s linear infinite;
}
@keyframes teer{
    0%{
        transform: translateY(-11px) translateX(15px);
    }
    50%{
        transform: translateY(-14px) translateX(42px);
    }
    100%{
        transform: translateY(-13px) translateX(77px);
    }
}


@media only screen 
       and (min-device-width: 280px)
       and (max-device-width:480px){
       
    .imgpricename{
        width: 85%;
        height: 100%;
        margin: 30px auto;
        box-shadow: 10px 10px 24px 0px rgba(0,0,0,0.75);
    }
    .namefooter{
        padding-left: 15px;
        font-weight: 900;
    }
    footer button{
        font-size: 13px;
        font-weight: 500;
        padding: 2px;
        margin-right: 6px;
    }
    footer button span{
        display: none;
    }
    footer .visit{
        font-weight: 500;
    }
   section h3{
       font-size: 14px;
       padding-top: 30px;
   }

}
